import topSearch from '../../components/topSearch.vue'

export default {
	name: "topSearch",
	components: {
		topSearch
	},
	data() {
		return {
			mapName:"萧山区",
			windowW:'',
			banner:{    //首页banner轮播
				indicatorDots: true,
				autoplay: true,
				circular:true,
				interval: 6000,
				duration: 500,
				displayItems: 1,
				previousM: '',
				nextM: '',
				indicatorColorOff:"#999999",
				indicatorColorOn:"#C90009"
			},
			notice:{    //活动公告
				autoplay:true,
				interval:5000,
				duration:300,
				circular:true,
				vertical:true
			},
			hdzx:{    //活动资讯
				autoplay:false,
				circular:true,
				interval: 6000,
				duration: 500,
				displayItems: 1,
				previousM:'',
				nextM: '',
			},
			syzx:{    //商业资讯
				autoplay:false,
				circular:true,
				interval: 6000,
				duration: 500,
				displayItems: 2,
				nextM: '',
			}
		}
	},
	created(){
		var _this=this;
	},
	mounted(){
		var _this=this;
		uni.getSystemInfo({
			success: function (res) {
				_this.windowW=res.windowWidth;
				_this.banner.previousM=_this.windowW*13/375;
				_this.banner.nextM=_this.windowW*20/375;
				_this.hdzx.previousM=_this.windowW*0/375;
				_this.hdzx.nextM=_this.windowW*85/375;
				_this.syzx.nextM=_this.windowW*0/375;
			}
		});
	},
	methods: {
		goClassPages:function(url){
			uni.navigateTo({
				url:url,
			})
		},
	}
}
